@use "base/variables" as v;
@use "base/mixins" as m;

.main-content {
    background-color: v.$white;
    margin-top: -30px;
    padding: 2.5rem 1.5rem;

    @include m.md { 
        padding: 3.5rem;
        margin-top: -100px;
    }

    @include m.lg { 
        p {
            width: 75%;
            margin: 0 auto;
        }
    }
}